import React from 'react'

import './card.scss'

const Card = ({ title, stack, description, href }) => {
  return (
    <div className='card'>
      <a href={href} target={!!href && '_blank'}>
        <p className='title'>{title}</p>
        <p className='stack'>{stack}</p>
        <span className='description'>{description}</span>
      </a>
    </div>
  )
}

export default Card
